<style lang="less" scoped>
  .noWrapper{
    text-align: center;
    &>img{
      width: 400px;
      margin: 40px 0 20px 0;
    }
    color: #aaa;
    font-size: 30px;
  }
  .flex_middle:nth-child(even){
    margin-left: 20px;
  }
  .flex_middle{
    &>.course_header_img{
      overflow: hidden;
      &>img{
        width: 100%;
      }
    }
  }
</style>

<template>
  <div>
    <!-- 课程列表 START -->
    <section class="courseList">
      <div class="course_flex">

        <live-single-course v-for="(item, index) in live" :key="index" :item="item"></live-single-course>

      </div>
      <p class="noWrapper" v-if="!live.length"><img src="../../assets/img/da.png" alt="没有关注的课程哦~"><br>暂时还没有内容~~宝宝先看看别的~~</p>
    </section>
  </div>
</template>
<script>
  import liveSingleCourse from '@/components/liveSingleCourse'
  export default {
    name: 'freePage',
    data() {
      return {
        courses: [],
        item: {
          courseId: null
        },
        live: [],
        title: '暂无直播',
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
      }
    },
    components: {
      liveSingleCourse
    },
    created() {
      
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.get('/api/courses/mylivebroadcast_vip', {
        params: {
          page: this.page
        }
      }).then(res => {
        this.live = res.data.result || []
        this.$Indicator.close()
      }).catch(err => {
        console.log(err);
      });
    },
    mounted () {
      window.onscroll = ()=>{
        this.dropDown()
      }
    },
    methods: {
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1;
          this.falgNoData = true;
          this.axios.get('/api/courses/mylivebroadcast_vip', {
            params: {
              page: this.page
            }
          }).then(res => {
            if (this.pagination) {
              if (res.data.result && res.data.result.length > 0) {
                this.live = this.live.concat(res.data.result)
                this.$Indicator.close()
                this.falgNoData =false
              } else {
                this.$Indicator.close()
                this.pagination = false
                this.showData = true
                this.falgNoData = true
              }
            }
          }).catch(err => {
            console.log(err)
          });
        }
      },
    }
  }
</script>



